@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');

*{
    font-family: 'Mulish', sans-serif;
    margin: 0 4px;
    padding: 0%;
    box-sizing: border-box;
}
.navbar{
    background-color: white !important;
}
.navbar-brand{
    font-size: 1.9rem !important;
    color: rgb(0, 140, 255)   !important;
}
.navbar a{
    font-size: 1.3rem;
}
.navbar a:hover{
    color: rgb(0, 140, 255)  !important;
}
.menu_active{
    font-weight: bold;
    border-bottom: 1px solid grey;
    color: rgb(0, 140, 255)   !important;
}

#header .btn-get-started:hover{
    background-color: rgb(0, 140, 255) ;
    color: white;
}
#header{
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}
#header h1{
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black;
}
#header .brand-name{
    color: #3498db;
}
#header h2{
    color: #484848;
    font-size: 24px;
}
#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color:  #3498db;
    border: 2px solid  #3498db;
    text-decoration: none;
}
.header-img{
    text-align: right;
}
#header .animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
@keyframes up-down {
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
.card-img-top{
    width: 20rem;
    height: 16rem;
}
label{
    margin-top: 12px;
}
.contactbtn{
    margin-top: 12px;
}